/** @fileoverview The recurrent payments DApp's main file
 * This app is developed to work with Closure Compiler and JSDoc
 * providing types checking, code optimisation and automatic generation of documentation,
 * for the details see please:
 * {@link https://jsdoc.app}
 * {@link https://github.com/google/closure-compiler}
 * {@link https://github.com/google/closure-compiler/wiki/Annotating-JavaScript-for-the-Closure-Compiler#type-expressions}
 * {@link https://github.com/google/closure-compiler/wiki/Types-in-the-Closure-Type-System}
 * @see module:type_def.h
 * @see module:templates/app
 * @see module:templates/transactions
 * @see module:templates/schedule
 * @see module:login_logout
 * @see module:utils
 * @see module:local_storage
 */
import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {render} from 'lit';
import AppTemplates from './templates/app.js';
import TransactionsTemplates from './templates/transactions.js';
import ScheduleTemplates from './templates/schedule.js';
import LogInOut from './login_logout.js';
import Utils from './utils.js';
import {BookedAddresses, ScheduleTable, ContractInstance} from './local_storage.js';
import './components/deploy_contract.js';
import './app.css';


/** The DApp singleton
 * @struct @const {Object} */
const DApp = (() => {
 // public outside of this object:
  /** @struct @const {Object} */
  const pub_ = {
    /** The DApp's template container
     * @const {Element} */
    tmpl: document.getElementById('appTemplate').content
  };

 // private outside of this object:
  /** The app's page elements
   * @struct @const {Object<string, (Element|Object)>} */
  const page = {
    /** @const {Element} */
    content: pub_.tmpl.querySelector("kor-page"),
    /** @const {Element} */
    notification: pub_.tmpl.getElementById('pageNotification'),
    /** The app's top bar
     * @const {Element} */
    top_bar: pub_.tmpl.querySelector("kor-app-bar"),
    /** The app's menu button elements
     * @struct @const {Object<string, Element>} */
    menu: {
      /** @const {Element} */
      button: pub_.tmpl.getElementById('appMenu'),
      /** The app's menu tooltip
       * @const {Element} */
      tooltip: pub_.tmpl.getElementById('appMenuTooltip'),
      /** The app's menu container elements
       * @struct @const {Object<string, Element>} */
      popover: {
        /** @const {Element} */
        content: pub_.tmpl.getElementById('appMenuPopover'),
        /** The login menu item
         * @const {Element} */
        login: pub_.tmpl.getElementById('loginMenuItem'),
        /** The prefs menu item
         * @const {Element} */
        prefs: pub_.tmpl.getElementById('prefsMenuItem')
      }
    }, // menu
    /** The add transaction button at the app's top bar
     * @const {Element} */
    add_transaction: pub_.tmpl.getElementById('addTransaction'),
    /** The switch chain button at the app's top bar
     * @const {Element} */
    switch_chain: pub_.tmpl.getElementById('switchChain'),
    /** The deposit Ethers button at the app's top bar
     * @const {Element} */
    deposit_ethers: pub_.tmpl.getElementById('depositEthers'),
    /** The deploy contract button at the app's top bar
     * @const {Element} */
    deploy_contract: pub_.tmpl.getElementById('deployContract'),
    /** The add transaction tooltip
     * @const {Element} */
    add_transaction_tooltip: pub_.tmpl.getElementById('addTransactionTooltip'),
    /** The summary container elements
     * @struct @const {Object<string, Element>} */
    summary: {
      /** @const {Element} */
      content: pub_.tmpl.getElementById('summaryContent'),
      /** @const {Element} */
      details: pub_.tmpl.getElementById('summaryDetails')
    },
    /** The transactions container elements
     * @struct @const {Object<string, Element>} */
    transactions: {
      /** @const {Element} */
      content: pub_.tmpl.getElementById('transactionsContent'),
      /** @const {Element} */
      details: pub_.tmpl.getElementById('transactionsDetails'),
      /** @const {Element} */
      filter_button: pub_.tmpl.getElementById('transactionsFilterButton')
    },
    /** The schedule container elements
     * @struct @const {Object<string, Element>} */
    schedule: {
      /** @const {Element} */
      content: pub_.tmpl.getElementById('scheduleContent'),
      /** @const {Element} */
      details: pub_.tmpl.getElementById('scheduleDetails'),
      /** @const {Element} */
      add_button: pub_.tmpl.getElementById('scheduleAddButton')
    },
    /** The app's tabs strip
     * @const {Element} */
    tab_strip: pub_.tmpl.querySelector("kor-tabs")
  }; // page
  /** The add transaction modal window elements
   * @struct @const {Object<string, Element>} */
  const add_transaction_modal = {
    /** @const {Element} */
    window: pub_.tmpl.getElementById('addTransactionModal'),
    /** @const {Element} */
    notification: pub_.tmpl.getElementById('addTransactionModalNotification'),
    /** @const {Element} */
    content: pub_.tmpl.getElementById('addTransactionModalContent'),
    /** @const {Element} */
    button: pub_.tmpl.getElementById('addTransactionModalButton')
  };
  /** The transaction details modal window
   * @const {Element} */
  const transaction_details_modal = pub_.tmpl.getElementById('transactionDetailsModal');
  /** The add schedule modal window elements
   * @struct @const {Object<string, Element>} */
  const add_schedule_modal = {
    /** @const {Element} */
    window: pub_.tmpl.getElementById('addScheduleModal'),
    /** @const {Element} */
    notification: pub_.tmpl.getElementById('addScheduleModalNotification'),
    /** @const {Element} */
    transactions_content: pub_.tmpl.getElementById('addScheduleModalTransactionContent'),
    /** @const {Element} */
    schedule_content: pub_.tmpl.getElementById('addScheduleModalScheduleContent'),
    /** @const {Element} */
    button: pub_.tmpl.getElementById('addScheduleModalButton')
  };
  /** The deposit ethers modal window elements
   * @struct @const {Object<string, Element>} */
  const deposit_ethers_modal = {
    /** @const {Element} */
    window: pub_.tmpl.getElementById('depositEthersModal'),
    /** @const {Element} */
    notification: pub_.tmpl.getElementById('depositEthersModalNotification'),
    /** @const {Element} */
    value: pub_.tmpl.getElementById('depositEthersModalValue'),
    /** @const {Element} */
    button: pub_.tmpl.getElementById('depositEthersModalButton')
  };
  /** A contract deployer component
   * @const {Element} */
  const deploy_contract = pub_.tmpl.querySelector("dapp-deploy-contract");
  deploy_contract.notification_container = page.notification;
  //const viewport_width = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
  //const viewport_height = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);

  // Render tabs into the tab strip
  render(AppTemplates.tab_strip(
    page.summary, page.transactions, page.schedule), page.tab_strip);

  // Check whether the dark theme should be applied
  if (page.content && window.matchMedia
      && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    page.content.setAttribute("theme", "dark");
    add_transaction_modal.window.setAttribute("theme", "dark");
    transaction_details_modal.setAttribute("theme", "dark");
    add_schedule_modal.window.setAttribute("theme", "dark");
    deposit_ethers_modal.window.setAttribute("theme", "dark");
    /** Style the native data picker to fit the dark theme
     * @const {Element} */
    const sheet = document.createElement('style');
    sheet.innerHTML = `
      input[type="date"] {
        background: transparent;
        color: white;
      }
      input[type="date"]::-webkit-calendar-picker-indicator {
        filter: invert(100%);
      }`;
    document.body.appendChild(sheet);
    deploy_contract.setAttribute("theme", "dark");
  }

  /** The object, which stores the deployed contract instance in the Local Storage
   * @type {ContractInstance}
   */
  let contract_instance = new ContractInstance();
  page.deploy_contract.addEventListener('click', () => {
    deploy_contract.setAttribute("visible", "true");
  });
  deploy_contract.addEventListener('contract-deployed', () => {
    contract_instance.value = deploy_contract.contract_instance;
    console.log("A contract deployed, the contract_instance == ", deploy_contract.contract_instance);
  });

  page.menu.button.addEventListener('click', () => {
    page.menu.tooltip.setAttribute("visible", "false");
  });

  /** The object to represent different web3 wrappers
   * @type {Web3_Wrappers} */
  let web3_wrappers = {};

  page.menu.popover.login.setAttribute("login", "true");
  /** @func loginlogout - An instantiated functional object
   */
  const loginlogout =
    LogInOut.Init(page.summary.details, page.transactions.details, page.schedule.details,
      page.menu.popover.login, page.add_transaction, page.switch_chain, page.deposit_ethers,
      transaction_details_modal, web3_wrappers);
  page.menu.popover.login.addEventListener('click', () => {
    // Close the app menu
    page.menu.popover.content.removeAttribute("visible");
    loginlogout();
  });

  /** The transaction's details temporary object
   * @type {TransactionData}
   */
  let transaction_data = {
    gas_price: 1,
    gas_limit: 21000
  };

  /** The addresses already used to make transactions
   * as an instance of the BookedAddresses class backed by the Local Storage
   * @type {BookedAddresses}
   */
  let booked_addresses = new BookedAddresses();

  page.add_transaction.addEventListener('click', () => {
    page.add_transaction_tooltip.setAttribute("visible", "false");
    render(TransactionsTemplates.add_transaction(
      6 /* account_balance */, [] /* own_addresses */,
      booked_addresses, transaction_data), add_transaction_modal.content);
    add_transaction_modal.window.setAttribute("visible", "true");
  });

  add_transaction_modal.window.addEventListener('visible-changed', (e) => {
    if (!e.target.visible) {
      /** @const {?Element} */
      const notification_item =
        add_transaction_modal.notification.querySelector('kor-notification-item');
      if (notification_item) notification_item.removeAttribute("visible");
    }
  });

  add_transaction_modal.button.addEventListener('click', () => {
    if (!Utils.check_transaction_data(transaction_data, add_transaction_modal.notification)) return;
    add_transaction_modal.window.removeAttribute("visible");
    console.log("The transaction's destination_address == ", transaction_data.destination_address);
    console.log("The transaction's amount == ", transaction_data.amount);
    if (transaction_data.destination_address && transaction_data.amount > 0) {
      /** The transaction object literal
       * @struct @const {Object} */
      const transactionObject = {
        /** @const {string|number} */
        from: Moralis.User.current().get('ethAddress'),
        /** @const {string} */
        to: transaction_data.destination_address,
        /** @const {number} */
        value: (transaction_data.amount * Math.pow(10, 18)),
        /** @const {number} */
        gasPrice: (transaction_data.gas_price * Math.pow(10, 9)),
        /** @const {number} */
        gas: transaction_data.gas_limit
      };
      console.log("The transactionObject == ", transactionObject);
      web3_wrappers.metamask.eth.sendTransaction(transactionObject, (error, result) => {
        console.log("The sendTransaction error == ", error);
        console.log("The sendTransaction result == ", result);
        if (!error) {
          booked_addresses.add(transaction_data.destination_address);
          render(
            AppTemplates.success_notifications(
              ["The transaction was sent and will be shown in the Transactions tab after processing"]),
            page.notification);
          /** @const {Element} */
          const notification_item = page.notification.querySelector('kor-notification-item');
          notification_item.setAttribute("visible", "true");
        }
        // Create a new object by copying of inner values
        /** @type {Object} */
        let tmp_data = {};
        Object.assign(tmp_data, transaction_data)
        transaction_data = tmp_data;
      });
    } // if values are filled
  }); // On click to the add_transaction_modal.button

  /** The schedule's details temporary object
   * @type {ScheduleData}
   */
  let schedule_data = {};

  /** The object, which stores the table of schedule items
   * as an instance of the ScheduleTable class backed by the Local Storage
   * @type {ScheduleTable}
   */
  let schedule_table = new ScheduleTable();
  /** @type {Function} */
  const refresh_schedule = ()=>{
    render(ScheduleTemplates.schedule_table(schedule_table, refresh_schedule), page.schedule.details);
  };
  refresh_schedule();

  page.schedule.add_button.addEventListener('click', () => {
    render(TransactionsTemplates.add_transaction(
      6 /* account_balance */, [] /* own_addresses */,
      booked_addresses, transaction_data), add_schedule_modal.transactions_content);
    render(ScheduleTemplates.add_schedule(
      schedule_data), add_schedule_modal.schedule_content);
    add_schedule_modal.window.setAttribute("visible", "true");
  });

  add_schedule_modal.window.addEventListener('visible-changed', (e) => {
    if (!e.target.visible) {
      /** @const {?Element} */
      const notification_item =
        add_schedule_modal.notification.querySelector('kor-notification-item');
      if (notification_item) notification_item.removeAttribute("visible");
    }
  });

  add_schedule_modal.button.addEventListener('click', () => {
    if (!Utils.check_transaction_data(transaction_data, add_schedule_modal.notification)) return;
    if (!Utils.check_schedule_data(schedule_data, add_schedule_modal.notification)) return;
    /** @const {ScheduleItem} */
    const schedule_item = {
      transaction_data: transaction_data,
      schedule_data: schedule_data
    };
    schedule_table.add(schedule_item);
    add_schedule_modal.window.removeAttribute("visible");
    render(ScheduleTemplates.schedule_table(
      schedule_table, refresh_schedule), page.schedule.details);
    // Create new objects by copying of inner values
    /** @type {Object} */
    let tmp_data = {};
    Object.assign(tmp_data, transaction_data)
    transaction_data = tmp_data;
    tmp_data = {};
    Object.assign(tmp_data, schedule_data);
    schedule_data = tmp_data;
  });

  /** @const {switchChain} */
  const switchChain = Utils.initMetamaskChainSwitch();
  page.switch_chain.addEventListener('click', () => {
    switchChain(1); // The Mumbai Testnet
  });

  page.deposit_ethers.addEventListener('click', () => {
    deposit_ethers_modal.window.setAttribute("visible", "true");
  });
  deposit_ethers_modal.button.addEventListener('click', () => {
    deposit_ethers_modal.window.removeAttribute("visible");
    /** @const {*} */
    web3_wrappers.matic.depositEther(
      (parseFloat(deposit_ethers_modal.value.value) * Math.pow(10,18)),
      { from: Moralis.User.current().get('ethAddress'),
        gasPrice: (10 * Math.pow(10,9)),
        gasLimit: 200000}
    ).then((response)=>{
      console.log("The deposit ethers response == ", response);
    });
  });

  // Populate the public object
  return pub_;
})(); // The DApp singleton

document.addEventListener('DOMContentLoaded', () => {
  // Moralis is bound in the app.html via the script tag,
  // because it is better not to include it into the ./node_modules,
  // as it has one package with low vulnerability (insecure credential storage),
  // a legacy cjs modules system and huge number of dependencies with duplicated code
  Moralis.initialize("pnj2ou0ivNkr2xnWIQIKZjwhcbJgC6hO69CasCTS" /* YOUR_APP_ID */);
  Moralis.serverURL = 'https://kcmeib0ovv6c.moralis.io:2053/server';

  // Move the app from the document fragment into the page's DOM,
  // when the styles are parsed
  const body = document.getElementsByTagName('body')[0];
  body.removeChild(document.getElementById('startupContent'));
  body.appendChild(DApp.tmpl);
}); // DOMContentLoaded
