/** @fileoverview Dynamic GUI definition for general purpose
 *  @externs
 *  @module
 *  @see module:../type_def.h
 */
import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html} from 'lit';

export default {
  /** Generates the tab strip with the data binding.
   *  Each tab, when activated, changes a visibility of an appropriate content.
   *  It is impossible to make content elements as a child of a some element-wrapper,
   *  because in that case <kor-table> hasn't an apropriate overflow,
   *  thus detailed views should have no more than a one element-wrapper within
   *  the first <kor-grid> on <kor-page> and it should be a native HTML element,
   *  so there is <div>.
   * @func tab_strip
   * @param {Object<string, Element>} summary
   * @param {Object<string, Element>} transactions
   * @param {Object<string, Element>} schedule
   * @return {Object}
   */
  tab_strip(summary, transactions, schedule) {
    return html`
      <kor-tab-item id="summaryTab" label="Summary" active
        @active-changed="${(e)=>{
          if (e.target.active) {
            Object.entries(transactions).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(schedule).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(summary).map(
              (key_value) => {
                key_value[1].style.display = "block";
            });
          }
        }}">
      </kor-tab-item>
      <kor-tab-item id="transactionsTab" label="Transactions"
        @active-changed="${(e)=>{
          if (e.target.active) {
            Object.entries(summary).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(schedule).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(transactions).map(
              (key_value) => {
                key_value[1].style.display = "block";
            });
          }
        }}">
      </kor-tab-item>
      <kor-tab-item id="scheduleTab" label="Schedule"
        @active-changed="${(e)=>{
          if (e.target.active) {
            Object.entries(summary).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(transactions).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(schedule).map(
              (key_value) => {
                key_value[1].style.display = "block";
            });
          }
        }}">
      </kor-tab-item>`;
  },
  /** Generate a table with two columns from Object.entries()
   *  array of [key, value] items
   * @func object_entries_two_columns
   * @param {Array<string, (string|number)>} object_entries_array
   * @return {Object}
   */
  object_entries_two_columns(object_entries_array) {
    return html`
      <kor-table columns="250px auto">
        <kor-table-row slot="header">
          <kor-table-cell head>Key</kor-table-cell>
          <kor-table-cell head>Value</kor-table-cell>
        </kor-table-row>
        ${object_entries_array.map(
          (key_value) => html`
            <kor-table-row>
              <kor-table-cell>${key_value[0]}</kor-table-cell>
              <kor-table-cell>${key_value[1]}</kor-table-cell>
            </kor-table-row>`
        )}
      </kor-table>`;
  },
  /** Generate an errors notification from the errors descriptions as an array of strings
   * @func error_notifications
   * @param {Array<string|Object>} errors
   * @return {Object}
   */
  error_notifications(errors) {
    return html`
      <kor-notifications position="top-right">
        <kor-notification-item icon="error" label="Error">
        ${errors.map(
          (error) => html`
            <kor-text size="body-1">${JSON.stringify(error)}</kor-text>`
        )}
        </kor-notification-item>
      </kor-notifications>`;
  },
  /** Generate a notification about successes from the descriptions as an array of strings
   * @func success_notifications
   * @param {Array<string|Object>} successes
   * @return {Object}
   */
  success_notifications(successes) {
    return html`
      <kor-notifications position="top-right">
        <kor-notification-item icon="done" label="Success">
        ${successes.map(
          (success) => html`
            <kor-text size="body-1">${JSON.stringify(success)}</kor-text>`
        )}
        </kor-notification-item>
      </kor-notifications>`;
  }
}
