/** @fileoverview The stuff related to the login and logout procedure
 *  @externs
 *  @module
 *  @see module:templates/app
 *  @see module:templates/transactions
 */
import {render, html} from 'lit';
import AppTemplates from './templates/app.js';
import TransactionsTemplates from './templates/transactions.js';

export default {
  /** The factory for a Function object
   * @func Init
   * @param {Element} summary_details The summary details container
   * @param {Element} transactions_content The transactions container
   * @param {Element} schedule_content The schedule container
   * @param {Element} login_menu The login menu item
   * @param {Element} add_transaction The add transaction button
   * @param {Element} switch_chain The switch chain button
   * @param {Element} deposit_ethers The deposit ethers button
   * @param {Element} transaction_details_modal The transaction details modal window
   * @param {Web3_Wrappers} web3_wrappers The object to represent different web3 instances connected to different providers
   * @return {Function} The function, which should be bound to a menu clicking event
   */
  Init(summary_details, transactions_content, schedule_content,
       login_menu, add_transaction, switch_chain, deposit_ethers,
       transaction_details_modal, web3_wrappers)
  {
    /** @type {Element} */
    const transaction_details_content =
      transaction_details_modal.querySelector("kor-card");
    /** @func
     * @param {Object} transaction
     * @return {Function} */
    const transaction_details = (transaction) => {
      render(
        AppTemplates.object_entries_two_columns(Object.entries(transaction.attributes)),
        transaction_details_content);
      transaction_details_modal.setAttribute("visible", "true");
    };
    /** @func getUserTransactions - async
     * @param {Object} user
     */
    const getUserTransactions =  async (user) => {
      /** Create the query for historical incoming transactions
       * @const {Object} */
      const query_in = new Moralis.Query("EthTransactions");
      query_in.equalTo("to_address", user.get("ethAddress"));
      /** Create the query for historical out coming transactions
       * @const {Object} */
      const query_out = new Moralis.Query("EthTransactions");
      query_out.equalTo("from_address", user.get("ethAddress"));
      /** Find incoming transactions
       * @type {Object}
       */
      let in_transactions = await query_in.find();
      console.log("Incoming transactions: ", in_transactions);
      /** Find out coming transactions
       * @type {Object}
       */
      let out_transactions = await query_out.find();
      console.log("Out coming transactions: ", out_transactions);
      // Render the transactions data into the transactions page
      render(
        TransactionsTemplates.transactions(in_transactions, out_transactions, transaction_details),
        transactions_content);
      /** Subscribe for the real-time updates of incoming transactions
       * @const {Object}
       */
      const in_subscription = await query_in.subscribe();
      /** Subscribe for the real-time updates of out coming transactions
       * @const {Object}
       */
      const out_subscription = await query_out.subscribe();
      in_subscription.on("create", (new_transaction) => {
        console.log("A new incoming transaction: ", new_transaction);
        in_transactions = in_transactions.concat(new_transaction);
        // Render the transactions data into the transactions page
        render(
          TransactionsTemplates.transactions(in_transactions, out_transactions, transaction_details),
          transactions_content);
      }); // OnInSubscription
      out_subscription.on("create", (new_transaction) => {
        console.log("A new out coming transaction: ", new_transaction);
        out_transactions = out_transactions.concat(new_transaction);
        // Render the transactions data into the transactions page
        render(
          TransactionsTemplates.transactions(in_transactions, out_transactions, transaction_details),
          transactions_content);
      }); // OnOutSubscription
      // Create a Web3 instance with a Metamask as the blockchain provider,
      // it is equal to the 'new Web3(window.ethereum)' invocation
      web3_wrappers.metamask = await Moralis.Web3.enable();
      console.log("web3_wrappers.metamask == ", web3_wrappers.metamask);
      // Create an SDK instance
      web3_wrappers.matic = new Matic({
        network: "testnet", // set network name
        version: "mumbai", // set network version
        // Set Matic provider - string or provider instance
        // Example: <network.Matic.RPC> OR new Web3.providers.HttpProvide(<network.Matic.RPC>)
        // Some flows like startExitFor[Metadata]MintableBurntToken, require a webSocket provider such as new web3 providers.WebsocketProvider('ws://localhost:8546')
        maticProvider: web3_wrappers.metamask,
        // Set Mainchain provider - string or provider instance
        // Example: 'https://ropsten.infura.io' OR new Web3.providers.HttpProvider('http://localhost:8545')
        parentProvider: web3_wrappers.metamask,
        // set default options e.g { from }
        parentDefaultOptions: {from: user.get("ethAddress")},
        // set default options
        maticDefaultOptions: {from: user.get("ethAddress")}
      });
      // Init matic
      web3_wrappers.matic.initialize().then(async () => {
        console.log("web3_wrappers.matic == ", web3_wrappers.matic);
      });
    };
    /** @func
     */
    const logOut = async () => {
      await Moralis.User.logOut();
      /// Clean the tabs content
      render(html``, summary_details);
      render(html``, transactions_content);
      render(html``, schedule_content);
      ///
      console.log("Logged out");
    };

    return () => {
      if (!login_menu.getAttribute("login")) {
        Moralis.LiveQuery.close(); // Close a WebSocket for all subscriptions
        logOut();
        login_menu.setAttribute("label", "Login using Metamask");
        login_menu.setAttribute("login", "true");
        add_transaction.style.display = "none";
        switch_chain.style.display = "none";
        deposit_ethers.style.display = "none";
        return;
      }
      // Log In:
      Moralis.Web3.authenticate().then((current_user) => {
        login_menu.setAttribute("label", "Log out");
        login_menu.removeAttribute("login");
        add_transaction.style.display = "block";
        switch_chain.style.display = "block";
        deposit_ethers.style.display = "block";
        // Render the auth data into the summary content
        render(
          AppTemplates.object_entries_two_columns(Object.entries(current_user)),
          summary_details);
        console.log("The current user: ", current_user);
        console.log("The current user password: ", current_user.password);
        console.log("The current user's EthAddress: ", current_user.get("ethAddress"));
        Moralis.Cloud.run(
          'watchEthAddress',
          {"address": current_user.get("ethAddress")
        }).then((results) => {
          console.log("The watchEthAddress results: ", results);
          getUserTransactions(current_user);
        }).catch(() => {
          // Maybe the address is watchable already
          getUserTransactions(current_user);
        }); // OnWatchAddress
      }); // OnAuth
    } // A lambda object is returned
  } // Init
}
